import OIDCProvider from "next-auth/providers/oidc"

export function assertOidcConfig() {
  if (!process.env.OIDC_CLIENT_ID || !process.env.OIDC_CLIENT_SECRET || !process.env.OIDC_ISSUER) {
    throw new Error("Missing OIDC configuration. Ensure OIDC_CLIENT_ID, OIDC_CLIENT_SECRET, and OIDC_ISSUER are set.")
  }
}

export function createAuthOptions() {
  assertOidcConfig()

  const provider = OIDCProvider({
    clientId: process.env.OIDC_CLIENT_ID,
    clientSecret: process.env.OIDC_CLIENT_SECRET,
    issuer: process.env.OIDC_ISSUER,
    profile(profile) {
      const id = profile?.sub ?? profile?.id ?? profile?.user_id
      return {
        id,
        name: profile?.name ?? profile?.preferred_username ?? profile?.given_name ?? profile?.email ?? id,
        email: profile?.email,
        image: profile?.picture ?? profile?.avatar,
      }
    },
  })

  return {
    providers: [provider],
    session: {
      strategy: "jwt",
    },
    pages: {
      signIn: "/auth/signin",
    },
    callbacks: {
      async jwt({ token, account, profile }) {
        if (account && profile) {
          token.id = profile?.sub ?? profile?.id ?? profile?.user_id ?? token.sub
        }
        return token
      },
      async session({ session, token }) {
        if (session?.user) {
          session.user.id = token?.id ?? token?.sub ?? session.user.id
        }
        return session
      },
    },
  }
}
